<template></template>
<script>
	export default {
		mounted() {
			let catalogbybody = [];
            let catalogbynewes= [];
			let Catalog =  [
                {
                    infotitle: "三种不自律的表现，足够毁了我",
                    shortcontent: "拖延症 我是一个严重的拖延症患者，拖延到什么程度？礼拜是让人最放松的日子，上班比较劳累，一般大家都会选择在这个日子去放松自己，周末前，我给自己",
                    time:"2019-4-8",
                    coverurl:"https://upload-images.jianshu.io/upload_images/16263448-417f43eff10a9733?imageMogr2/auto-orient/strip%7CimageView2/2/w/600/format/webp",
                    bgcolor:"#5b9f9d"
                },{
                    infotitle: "后来，你是不是很少发朋友圈？",
                    shortcontent: "不知道从什么时候开始，我不再喜欢发朋友圈，大概是因为朋友圈里面没有真正能够体会自己的人，或者说自己不愿意去分享，又或者说自己已经不在乎朋友圈里面是否还有自己的位置，渐渐的，朋友圈就失去了我的踪影，不喜欢评论，不喜欢点赞，只是默默的逛一圈就走了，没有留下任何痕迹",
                    time:"2019-4-26",
                    coverurl:"http://p3.pstatp.com/large/pgc-image/2719c863ac0b41c28ccc34ed3b33de79",
                    bgcolor:"#778352"
                },{
                    infotitle: "如果你越来越沉默，说明你越来越成熟",
                    shortcontent: "很多人，总要经历从口若悬河到沉吟不语的过程，不知从什么时候开始，变得越来越沉默，不想去争执，不想去辩论。沉默的人并非冷漠",
                    time:"2019-4-26",
                    coverurl:"http://p3.pstatp.com/large/pgc-image/63eebfaef6a6451c8075ada320479151",
                    bgcolor:"#a1bcd2"
                }
            ]
			for( let i = 0; i < Catalog.length; i++ ){
                if( i <= 5 ){
                    catalogbybody.push({
                        id: i,
                        url: '/a'+i,
                        infotitle: Catalog[i].infotitle,
                        shortcontent: Catalog[i].shortcontent,
                        time: Catalog[i].time,
                        coverurl: Catalog[i].coverurl,
                        bgcolor: Catalog[i].bgcolor
                    });
                }
                catalogbynewes.push({
                    id: i,
                    url: '/a'+i,
                    infotitle: Catalog[i].infotitle,
                    shortcontent: Catalog[i].shortcontent,
                    time: Catalog[i].time,
                    coverurl: Catalog[i].coverurl,
                    bgcolor: Catalog[i].bgcolor
                });
			}
			this.$emit('setToBody', catalogbybody)
            this.$emit('setToNewes', catalogbynewes)
		}
	};
</script>


